<template>
    <div class="address">
       <Header :FlagAddress=true :FlagLogin=false :FlagSeek=false :FlagMine=false @dd='xx($event)'></Header>
       <div class="ding"></div>
       <div class="nei" v-for="(v,i) in arr" :key="i">
           <p>{{v.address_detail}}</p>
           <span>{{v.phone}}</span>
           <b v-if="Flag" @click="del(i,v.id)">×</b>
       </div>
       <router-link to="/add">
       <div class="add">
           <span>新增地址</span>
           <span><van-icon name="arrow" /></span>
       </div>
       </router-link>
       
    </div>
</template>

<script>
import Header from "./../../Unify/Header"
    export default {
        data(){
            return{
                Flag:'',
                arr:[],
            }
        },
        components:{
            Header
        },
        methods: {
            xx(res){
                console.log(res)
                this.Flag=res
            },
            del(i,id){
                    var user_id=this.$store.state.ma.user_id;
                this.$http.delete("http://elm.cangdu.org/v1/users/"+user_id+"/addresses/"+id+"").then((res)=>{
                    console.log(res);
                    if(res.data.success){
                        this.arr.splice(i,1)
                    }
                })
            }
            
            
        },
        mounted() {
           var user_id=this.$store.state.ma.user_id;
            this.$http.get("http://elm.cangdu.org/v1/users/"+user_id+"/addresses").then((res)=>{
                console.log(res);
                
                this.arr=res.data
            })
            
        },
    }
</script>

<style lang="scss" scoped>
.ding{
    width: 100%;
    height: 0.1rem;
    margin-top:0.6rem;
}
.nei{
    width: 100%;
    background: rgb(243, 248, 189);
    // margin-top: 0.7rem;
    height: 0.7rem;
    border-top:solid 1px #ccc;
    border-bottom:solid 1px #ccc;
    padding-left:0.2rem;
    padding-top: 0.2rem;
    position: relative;

    b{
        position: absolute;
        top:0.3rem;
        right: 0.5rem;
    }
}
.add{
    width: 100%;
    background: #fff;
    margin-top: 0.1rem;
    height: 0.6rem;
    line-height: 0.6rem;
    border-top:solid 1px #ccc;
    border-bottom:solid 1px #ccc;
    padding-left: 0.2rem;
    // margin-top:0.6rem;
    span{
        font-size: 0.24rem;
        color: #000;
        i{
            margin-left: 3rem;
            color: #ccc;
        }
    }
}
</style>